                            <div class=" flex-1 bg-white bg-opacity-20 rounded-lg p-4 mb-4 ml-2">
                                <div class="flex items-center justify-between">
                                    <div class="flex items-center"><img style="width: 30px;height: 30px;"
                                            src="@/assets/usdt.png" alt="USDT" class="w-12 h-12 mr-3">
