                                        alt="" class="w-12 h-12 mr-3">
                                    <div class="flex flex-col">
                                        <span class="text-sm">团队奖励USDT</span>
                                        <div class="flex mt-1"><span class="text-2xl font-bold">{{ teamRewardUSDT
                                                }}</span></div>
                                    </div>
                                </div>
