                        </div>
                        <div
                            class=" bg-white flex items-center justify-between bg-opacity-20 rounded-lg p-4 mb-4  data-item">
                            <div class="flex justify-between">
                                <div class="flex"><img style="width: 30px;height: 30px;" src="@/assets/data/data4.png"
                                        alt="" class="w-12 h-12 mr-3">
                                    <div class="flex flex-col"><span class="text-sm">团队奖励AIP</span>
                                        <div class="flex mt-1"><span class="text-2xl font-bold">{{ teamRewardToken
                                                }}</span></div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="toastShow" class="toast-message">{{ toastText }}</div>
    </div>
</template>

<script>
import {
    mapState
} from 'vuex'
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: 'Home',
    data: () => ({
        toastShow: false,
        toastText: '',
        isLoading: false,
        globalData: {},
        registerCount: 0,
        isClaimLoading: false,
        unClaimTokenRewards: 0,
        unClaimUSDTRewards: 0
    }),
    computed: {
        ...mapState(['user', 'walletAddress', 'isConnected', 'isChainCorrect', "isRegistered", 'walletAddress', 'poolInstance']),
        walletAddressShow() {
            return this.walletAddress.substr(0, 5) + "..." + this.walletAddress.substr(-5)
        },
        rewardCount() {
            console.log(this.user)
            if (this.user) {
                console.log(this.user.tokenRewardCount)
                return this.user.tokenRewardCount || 0;
            } else {
                return 0;
            }
        },
        pointReward() {
            if (this.user) {
                return this.user.pointCount || 0;
            } else {
                return 0;
            }
        },
        level() {
            if (this.user) {
                return this.user.pointCount ? this.calcLevel(this.user.pointCount) : 0;
            } else {
                return 0;
            }
        },
        teamRewardToken() {
            let totalReward = 0;
            if (this.level > 0) {
                for (let i = 1; i <= this.level; i++) {
                    this.$appConfig.levelRewardConfig[`l` + 10]
                    if (i > 10) {
                        totalReward += this.$appConfig.levelRewardConfig[`l` + 10].token;
                    } else {
                        totalReward += this.$appConfig.levelRewardConfig[`l` + i].token;
                    }
                }
                return totalReward;
            } else {
                return 0;
            }
        },
        teamRewardUSDT() {
            let totalReward = 0;
            if (this.level > 0) {
                for (let i = 1; i <= this.level; i++) {
                    console.log(this.$appConfig.levelRewardConfig[`l` + 10])
                    if (i > 10) {
                        totalReward += this.$appConfig.levelRewardConfig[`l` + 10].usdt;
                    } else {
                        totalReward += this.$appConfig.levelRewardConfig[`l` + i].usdt;
                    }
                }
                return totalReward;
            } else {
                return 0;
            }
        }
    },
    watch: {
        poolInstance(val) {
            if (val) {
                this.getGlobalData();
                this.getRegisterCount();
                this.calcRewards();
            }
        }
    },
    created() {
        if (this.poolInstance) {
            this.getGlobalData();
            this.getRegisterCount();
            this.calcRewards();
        }
    },
    methods: {
        calcLevel(pointCount) {
            let level = 0;
            if (pointCount > this.$appConfig.levelList[10]) {
                level = 10 + Math.floor(pointCount / 100000);
                return level
            } else {
                this.$appConfig.levelList.forEach((item, index) => {
                    if (pointCount >= item) {
                        level = index + 1;
                    }
                })
                return level;
            }

        },
        async copyLink() {
            await navigator.clipboard.writeText(this.inviteLink);
            // this.toastText = this.$t('common.copySuccess');
            this.showToast("复制成功");
        },
        showToast(text) {
            this.toastText = text;
            this.toastShow = true;
            setTimeout(() => {
                this.toastShow = false;
                this.toastText = "";
            }, 2000);
        },
        getGlobalData() {
            this.poolInstance.getGlobalData().then((data) => {
                console.log(data);
                if (data.length > 0) {
                    this.globalData = {
                        totalCheckInCount: data[0],
                        totalTokenDistributed: data[1],
                        totalPointDistributed: data[2]
                    };
                }
            });
        },
        getRegisterCount() {
            this.poolInstance.getRegisterCount().then((data) => {
                console.log(data);
                if (data > 0) {
                    this.registerCount = data || 0;
                }
            });
        },
        calcRewards() {
            this.poolInstance.calcRewards().then((data) => {
                console.log(data);
                this.unClaimUSDTRewards = Number(data[0]);
                this.unClaimTokenRewards = Number(data[1]);
            });
        },
        claimReward() {
            this.isClaimLoading = true;
            this.poolInstance && this.poolInstance.claimReward().then(async (tx) => {
                console.log(tx);

                const receipt = await tx.wait();
                if (receipt.status === 1) {
                    this.showToast("领取成功");
                    console.log("领取奖励成功！區塊號:", receipt.blockNumber);
                    this.$store.dispatch("getUserInfo");
                    this.showBind = false;
                } else {
                    console.log(receipt.status);
                }
                this.isClaimLoading = false;
            }).catch(err => {
                this.isClaimLoading = false;
                console.log("领取失败", err);
                this.showToast("领取失败");
            });
        }
    }
};
</script>

